import React from 'react';
import styles from './frame.css';
import classnames from 'classnames';

let sizeClasses = [styles.small, styles.medium, styles.big];
let colorClasses = [styles.green, styles.orange, styles.teal];
let directions = ['normal', 'reverse'];

function Ball({
  color,
  size,
  positionX,
  positionY,
  opacity,
  duration,
  direction,
}) {
  let style = {
    top: `${positionY}%`,
    left: `${positionX}%`,
    opacity: opacity,
    animationDuration: `${duration}s`,
    animationDirection: directions[direction],
  };
  return (
    <div
      style={style}
      className={classnames(
        styles.ball,
        sizeClasses[size],
        colorClasses[color],
      )}
    />
  );
}

function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}

function getAnimationDuration(max) {
  let min = 40;
  let maxAmount = Math.floor(max);
  return Math.floor(Math.random() * (maxAmount - min)) + min;
}

function opacity(number) {
  return number / 100 - 0.6 + 0.1;
}

class AnimatedBackground extends React.Component {
  state: {
    width: number;
    height: number;
  };

  constructor(props) {
    super(props);
    this.state = {
      width: 0,
      height: 0,
    };
  }

  componentDidMount() {
    this.setState({
      width: window.innerWidth,
      height: window.innerHeight,
    });
  }

  render() {
    if (!this.state.width && !this.state.height) {
      return null;
    }

    let length = Math.ceil(this.state.width / 7);

    return (
      <div className={styles.bgWrap}>
        <div className={styles.bg}>
          {[...Array(length).keys()].map((i) => (
            <Ball
              key={i}
              direction={getRandomInt(2)}
              duration={getAnimationDuration(200)}
              opacity={opacity(getRandomInt(100))}
              positionX={getRandomInt(this.state.width / 100)}
              positionY={getRandomInt(this.state.height / 100)}
              color={getRandomInt(3)}
              size={getRandomInt(3)}
            />
          ))}
        </div>
      </div>
    );
  }
}

export default AnimatedBackground;
